body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img,
abbr {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
legend {
  color: #000;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}
img {
  max-width: 100%;
  height: auto;
}
.wrapper {
  max-width: 1100px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}
@media screen and (max-width: 1100px) {
  .wrapper {
    padding-left: 6px;
    padding-right: 6px;
  }
}
@media screen and (max-width: 780px) {
  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media screen and (max-width: 450px) {
  .wrapper {
    padding-left: 4px;
    padding-right: 4px;
  }
}
/*.debug { .debug( rgba(0, 0, 0, .2), rgba(255, 255, 0, 0.6)) }*/
html {
  font-size: 14px;
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
}
html,
body {
  width: 100%;
  height: auto;
}
body {
  background: url('../img/bg6.jpg') center top no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
}
.header {
  background-color: transparent;
  width: 100%;
  height: auto;
}
.header__wrapper {
  min-height: 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-left: 10px;
  padding-right: 10px;
}
@media screen and (max-width: 1100px) {
  .header__wrapper {
    padding-left: 6px;
    padding-right: 6px;
  }
}
@media screen and (max-width: 780px) {
  .header__wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media screen and (max-width: 450px) {
  .header__wrapper {
    padding-left: 4px;
    padding-right: 4px;
  }
}
.header__logo {
  min-width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  flex-grow: 3;
}
.header__logo > img {
  height: calc(80px);
}
@media screen and (max-width: 450px) {
  .header__logo {
    justify-content: center;
  }
}
.header__buttons {
  min-width: 320px;
  height: calc(80px);
  display: flex;
  flex-basis: 400px;
  flex-grow: 1;
  justify-content: space-between;
}
.header__btn {
  background: transparent;
  text-decoration: none;
  font-size: 2rem;
  font-weight: 100;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: rgba(250, 250, 250, 0.95);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 2.5px;
  margin-right: 2.5px;
  width: calc(20% - 5px);
}
@media screen and (max-width: 1100px) {
  .header__btn {
    margin-left: 2.5px;
    margin-right: 2.5px;
  }
}
@media screen and (max-width: 780px) {
  .header__btn {
    margin-left: 2px;
    margin-right: 2px;
  }
}
@media screen and (max-width: 450px) {
  .header__btn {
    margin-left: 1px;
    margin-right: 1px;
  }
}
@media screen and (max-width: 350px) {
  .header__btn {
    width: calc(18.333333333333336% - 2px);
  }
}
.header__personal {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
/*  CONTAINER  */
.container {
  width: 100%;
  height: auto;
  margin: 0 auto 50px;
}
.navigation {
  min-height: 80px;
  background: rgba(250, 250, 250, 0.95);
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.nav__menu {
  flex-grow: 2;
  min-width: 200px;
  flex-basis: 300px;
  height: calc(60px);
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 20px;
}
@media screen and (max-width: 450px) {
  .nav__menu {
    justify-content: center;
  }
}
.nav__menu:after {
  content: '❯';
  color: #646464;
  display: block;
  height: auto;
  width: 8px;
  position: absolute;
  top: calc(20px);
  right: 0;
}
.nav__menu:before {
  content: '❮';
  color: #646464;
  display: block;
  height: auto;
  width: 8px;
  position: absolute;
  top: calc(20px);
  left: 0;
}
.nav__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-basis: 70px;
  min-width: 30px;
  height: calc(70px);
  /*img { margin: 0 0 auto 0 }*/
}
.nav__search {
  flex-grow: 2;
  min-width: 200px;
  flex-basis: 300px;
  height: calc(60px);
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.nav__search > input {
  width: 80%;
  height: calc(40px);
  padding: 0 20px;
}
@media screen and (max-width: 780px) {
  .nav__search {
    justify-content: center;
  }
}
.holder {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  background: rgba(250, 250, 250, 0.45);
  padding-top: 10px;
  padding-bottom: 10px;
  /*flex-direction: column;
  flex: 1 0 auto;
  overflow-x: hidden;
  overflow-y: auto;
  position: relative;
  z-index: 0;*/
}
.cell {
  background-color: rgba(220, 220, 220, 0.95);
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 2.5px;
  margin-right: 2.5px;
  width: calc(20% - 5px);
  margin: 2.5px;
}
@media screen and (max-width: 1100px) {
  .cell {
    margin-left: 2.5px;
    margin-right: 2.5px;
  }
}
@media screen and (max-width: 780px) {
  .cell {
    margin-left: 2px;
    margin-right: 2px;
  }
}
@media screen and (max-width: 450px) {
  .cell {
    margin-left: 1px;
    margin-right: 1px;
  }
}
@media screen and (max-width: 1100px) {
  .cell {
    width: calc(25% - 5px);
  }
}
@media screen and (max-width: 960px) {
  .cell {
    width: calc(25% - 5px);
  }
}
@media screen and (max-width: 780px) {
  .cell {
    width: calc(33.333333333333336% - 4px);
  }
}
@media screen and (max-width: 560px) {
  .cell {
    width: calc(50% - 4px);
  }
}
@media screen and (max-width: 450px) {
  .cell {
    width: calc(50% - 2px);
  }
}
@media screen and (max-width: 350px) {
  .cell {
    width: calc(100% - 2px);
  }
}
@media screen and (max-width: 1100px) {
  .cell {
    margin: 2.5px;
  }
}
@media screen and (max-width: 780px) {
  .cell {
    margin: 2px;
  }
}
@media screen and (max-width: 450px) {
  .cell {
    margin: 1px;
  }
}
